import { useState, useEffect } from "react";
import {encryptData, decryptData} from "./enc/crypt.js"
import {commitFile} from "./gh/gh.js"
import Login from "./Login/Login"
import EditScreen from "./Screens/EditScreen"


function App() {
  const [text, setText] = useState("");
  const [password, setPassword] = useState("");
  const [screen, setScreen] = useState("encrypt");
  const [db, setDb] = useState("");
  const [clearDb, setClearDb] = useState("");
  const [loggedIn, setLoggedIn] = useState("false");
  const [encrptedData, setEncrptedData] = useState("");
  const [decrptedData, setDecrptedData] = useState("");
  // const [date, setDate] = useState(new Date())

  const commitCallback = () => {
    console.log("Commit callback");
    console.log("clear", decryptData(db.d2, password));
    const repository = "sandrolex/sandrolex.github.io";
    const branch = "main";
    const filePath = "db.json";
    const content = JSON.stringify(db);
    const pat = decryptData(db.d1, password);
    commitFile(repository, branch, filePath, content, pat)
  }

  const dataHandle = (data) => {
    console.log("RECEIVED DATA", data);
    const cleartxt = decryptData(db.d2, password);
    const month = ["January","February","March","April","May","June","July","August","September","October","November","December"];
    const YY = data.date.getFullYear();
    const MM = month[data.date.getMonth()];
    const DD = data.date.getDate();
    const root = {
      f: data.food,
      s: data.sport,
      a: data.alcool,
      t: data.tabac,
      sl: data.sleep,
      m: data.mood,
      w: data.weight,
    }

    console.log(cleartxt);
    let clear = JSON.parse(cleartxt)
    console.log("clearDb", clear);
    
    if (!(YY in clear)) {
      clear[YY] = {};
    }
    if (! (MM in clear[YY])) {
      clear[YY][MM] = {};
    }
    clear[YY][MM][DD] = root;
    db.d2 = encryptData(JSON.stringify(clear), password);

  }

  const SendPassword = (data) => {
    setPassword(data);
    const verif = decryptData(db.d3, password);
    if (verif === "true") {
      setLoggedIn("true");
      setScreen("edit");
    } else {
      console.log("failed")
    }
  }

  useEffect(() => {
    const host = window.location.host;
    let protocol = "http://";
    if (host.includes("github") ) {
      protocol = "https://";
    }
    const dbUrl = protocol + host + '/db.json';
    fetch(dbUrl).then(
		  function (res) {
			  return res.json();
		  },
    ).then(function (data) {
      // store Data in State db Variable
      setDb(data);
    }).catch(
      function (err) {
        console.log(err, ' error');
      },
    );
  }, []);

  const switchScreen = (type) => {
    setText("");
    setEncrptedData("");
    setDecrptedData("");
    setScreen(type);
  };

  const handleClick = () => {
    if (!text) return;

    if (screen === "encrypt") setEncrptedData(encryptData(text, password));
    else { 
      setDecrptedData(decryptData(text, password));
      console.log("RAW", db);
      console.log("XXXX", decryptData(db.d2, password));
    }
  };

  const handleSaveDbClick = () => {
    console.log("clearDB", clearDb);
    db.d2 = encryptData(clearDb, password);
    console.log("cipher", db);
    const repository = "sandrolex/sandrolex.github.io";
    const branch = "main";
    const filePath = "db.json";
    const content = JSON.stringify(db);
    const pat = decryptData(db.d1, password);
    commitFile(repository, branch, filePath, content, pat)
  };

  const editScreen = () => {
    return (
      <div className="card">
        <input
          value={clearDb}
          name="edit"
          type="text"
          onChange={({ target }) => {
            setClearDb(target.value);
          }}
        />
        <button className="btn submit-btn" onClick={handleSaveDbClick}>
            Save
          </button>
      </div>
    )
  }
  const cryptScreen = () => {
    return (
      <div className="card">
          <input
            value={text}
            onChange={({ target }) => {
              setText(target.value);
            }}
            name="text"
            type="text"
            placeholder={
              screen === "encrypt" ? "Enter Text" : "Enter Encrypted Data"
            }
          />
          <input
            value={password}
            onChange={({ target }) => {
              setPassword(target.value);
            }}
            name="pass"
            type="text"
            placeholder="secret"
          />

          <button className="btn submit-btn" onClick={handleClick}>
            {screen === "encrypt" ? "Encrypt" : "Decrypt"}
          </button>
          {encrptedData || decrptedData ? (
          <div className="content">
            <label>{screen === "encrypt" ? "Encrypted" : "Decrypted"} Data</label>
            <p>{screen === "encrypt" ? encrptedData : decrptedData}</p>
          </div>
        ) : null}
        </div>

    )
  }
  
  const showScreen = (selection) => {
    if (selection === "edit") {
      return editScreen();
    } else if (selection === "calendar") {
      return (
        <EditScreen dataHandle={dataHandle} commitCallback={commitCallback}/>
      );
    } 
    return cryptScreen();
  }

  const frameScreen = () => {
    return (
      <div className="container">
        <div>
          <button
            className="btn btn-left"
            style={{
              backgroundColor: screen === "encrypt" ? "#5e35b1" : "#5e35b130",
            }}
            onClick={() => {
              switchScreen("encrypt");
            }}
          >
            Encrypt
          </button>

          <button
            className="btn btn-right"
            style={{
              backgroundColor: screen === "decrypt" ? "#1e88e5" : "#1e88e530",
            }}
            onClick={() => {
              switchScreen("decrypt");
            }}
          >
            Decrypt
          </button>
          
          <button
            className="btn btn-right"
            style={{
              backgroundColor: screen === "edit" ? "#1e88e5" : "#1e88e530",
            }}
            onClick={() => {
              setClearDb(decryptData(db.d2, password));
              switchScreen("edit");
            }}
          >
            Edit
          </button>

          <button
            className="btn btn-right"
            style={{
              backgroundColor: screen === "calendar" ? "#1e88e5" : "#1e88e530",
            }}
            onClick={() => {
              switchScreen("calendar");
            }}
          >
            Calendar
          </button>

        </div>
        {showScreen(screen)}
        
      </div>
    );
    }
    if (loggedIn === "false") {
      return (
        <Login SendPassword={SendPassword} />
      )
    } else {
      return frameScreen();
    }
    
}

export default App;


