import { useState} from "react";
import Calendar from 'moedim';
import FormScreen from "./FormScreen";

// function EditScreen({dataHandle}) {
const EditScreen = props => {
    const [date, setDate] = useState(new Date());

    const ReceiveDate = (data) => {
        setDate(data);
    }

    return (
        <div className="card">
            <Calendar value={date} onChange={(d) => { ReceiveDate(d) } }/>
            <FormScreen date={date} dataHandle={props.dataHandle} commitCallback={props.commitCallback}/>
        </div>
    );
}


export default EditScreen;