import { useState} from "react";

const FormScreen = props => {
    const [food, setFood] = useState(0);
    const [sport, setSport] = useState(0);
    const [alcool, setAlcool] = useState(0);
    const [tabac, setTabac] = useState(0);
    const [sleep, setSleep] = useState(0);
    const [mood, setMood] = useState(0);
    const [weight, setWeight] = useState(0);

    const handleCommitClick = () => {
        console.log("commit clik");
        props.commitCallback();
    }

    const handleAddClick = () => {
        const data = {
            food: parseInt(food), 
            sport: parseInt(sport), 
            alcool: parseInt(alcool), 
            tabac: parseInt(tabac), 
            sleep: parseInt(sleep),
            mood: parseInt(mood),
            weight: parseInt(weight),
            date: props.date
        }
        props.dataHandle(data);
    }

    return (
        <div>
            <p>{props.date.toLocaleDateString('en-GB')}</p> 
            <div onChange={(e) => setFood(e.target.value)}>
                Food: 
                <input type="radio" value="1" name="food" /> 1
                <input type="radio" value="2" name="food" /> 2
                <input type="radio" value="3" name="food" /> 3
            </div>
            <div onChange={(e) => setSport(e.target.value)}>
                Sport: 
                <input type="radio" value="1" name="sport" /> 1
                <input type="radio" value="2" name="sport" /> 2
                <input type="radio" value="3" name="sport" /> 3
            </div>
            <div onChange={(e) => setAlcool(e.target.value)}>
                Alcool: 
                <input type="radio" value="1" name="alcool" /> 1
                <input type="radio" value="2" name="alcool" /> 2
                <input type="radio" value="3" name="alcool" /> 3
            </div>
            <div onChange={(e) => setTabac(e.target.value)}>
                Tabac: 
                <input type="radio" value="1" name="tabac" /> 1
                <input type="radio" value="2" name="tabac" /> 2
            </div>
            <div onChange={(e) => setSleep(e.target.value)}>
                Sleep: 
                <input type="radio" value="1" name="sleep" /> 1
                <input type="radio" value="2" name="sleep" /> 2
            </div>
            <div onChange={(e) => setMood(e.target.value)}>
                Mood: 
                <input type="radio" value="1" name="mood" /> 1
                <input type="radio" value="2" name="mood" /> 2
            </div>
            <div onChange={(e) => setWeight(e.target.value)}>
                Weight: 
                <input type="text" name="weight"/>
            </div>
            <button onClick={handleAddClick}>Add</button>
            <br/>
            <button onClick={handleCommitClick}>Commit</button>
        </div>
    );
}

export default FormScreen;
