import axios from "axios";

export async function commitFile(repository, branch, filePath, content, pat) {
    const accessToken = pat;
    console.log("AT", accessToken);
    try {
    // Get reference SHA
    const refResponse = await axios.get(
        `https://api.github.com/repos/${repository}/contents/${filePath}`,
        {
            headers: {
                Authorization: `Token ${accessToken}`
            }
        }
    );
    
    const refSha = refResponse.data.sha;
    
    await axios.put(
        `https://api.github.com/repos/${repository}/contents/${filePath}`,
        {
            message: "commit from js",
            content: btoa(content),
            sha: refSha
        },
        {
            headers: {
                Authorization: `Token ${accessToken}`
                }
        }
    )
    
    console.log("File committed successfully!");
    } catch (error) {
    console.error(error);
}
}

